import React,{useState,useEffect}  from "react";
import {  Container } from '@material-ui/core';
import './styles/home.css';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import fire from "./config/fire";
import profile from '../img/profile.png';
import {Link} from 'react-router-dom';
import Loader from './loader';



const Home =()=>{
    const[post,setPost]=useState('');
    const [data,setData]=useState([]);
    const [loading ,setLoading]=useState(undefined);



    const handleChange=(e)=>{
            setPost(e.target.value);
    }



    const CreatePost =(e)=>{
        e.preventDefault();
            const postRef=fire.database().ref("Post");
            const posts={
                post,
                complete:false,
            };
            postRef.push(posts);
            
    }

    useEffect(()=>{
        const postRef=fire.database().ref("Post");
        postRef.on('value',(snapshot)=>{
            const data=snapshot.val();
            const datalist=[];
            for(let id in data){
                datalist.push(data[id]);
            }
            setData(datalist);
            setLoading(true);
           
        })
    },[])

    return(
        <div>  
           <Container maxWidth="md" style={{paddingTop:'20px'}}>
           <Link to="/account">
          <div className="accountsection text-right" data-hover=" My Account">
              <span ><img  src={profile} alt="My Accounts" style={{width:'60px',height:'60px',paddingBottom:'10px'}} /></span>
          </div>
          </Link>
               <Card style={{border: '1px solid'}}>
                   <CardContent>
              
                       
           <div className="tweetbox">
            <form action="">
                <div className="tweetbox_input">
                <img src="data:image/jpeg;base64," alt="" />
                <input type="text" placeholder="What's happening?" onChange={handleChange} value={post} required/>
            </div>
            <button className="tweetbox_tweetbutton" onClick={CreatePost}>Post</button>
            </form>
        </div>
        </CardContent>
               </Card>
               <hr></hr>
               <div className="content">
                   <Card style={{border: '1px solid #FFF'}}>
                       <CardContent style={{border: '1px solid'}}>
                          
                           <div>
                           {!loading ? <Loader/> :
                                   <>
                               
                                    {data.map((res,index)=>{
                                        return(
                                    <>
                                   
                               
                       <div className="tweetbox_input" >
                           <img src="data:image/jpeg;base64," alt="" />
                <h3 key={index}> {res.post}</h3>
                </div>
                <hr></hr>
               
                
               
                </>
                )})}
                </>
}
                         
                            </div>
                                 
                       </CardContent>
                   </Card>
                   </div>
            </Container> 
              
        </div>
    )
}

export default Home;